<template>
    <div>
        <header>
            <p></p>
            <p id="nickname"></p>
            <router-link to="/home">
                <span></span>
            </router-link>
            <div @click="postWeiboContent">
                
            </div>
        </header>
        <main>
            <textarea placeholder="......" maxlength="160" v-model="weiboContent"></textarea>
            <div class="input">
                <div class="export">
                        <form method="post" action="localhost:80/user/publish/upload" enctype="multipart/form-data">
                          
                                <input type="file" name="files" id="file"/>
                         
                            <input type="submit" id="submit"/>
                        </form>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
import { Toast } from "mint-ui";
export default{
    name: 'publish',
    data(){
        return{
            weiboContent:''
        }
    },
    methods:{
        //
        postWeiboContent(){
            if(this.weiboContent.trim().length === 0){
                return Toast("");
            }else{
                this.$http.post("user/publish/insert",{'weiboContent':this.weiboContent})
                .then(function(result){
                    if(result.body.status == 1){
                        console.log("");
                        this.$router.push('/home');
                    }
                })
            }
        }
    },
    created(){
        //
        this.$http.get('user/publish/nickname').then((result)=>{
            var nickname = document.getElementById('nickname');
            console.log(result.body.object)
            nickname.innerHTML = result.body.object;
        })
    } 
}  
</script>

<style scpoed>
 header{
    width:100%;
    height:8vh;
    position:relative;
    background-color:#f9f9f9;
    border-bottom:1px #d0d0d0 solid;
}
header p:nth-of-type(1){
    line-height:5vh;
    text-align:center;
    font-size:18px;
}
header p:nth-of-type(2){
    text-align:center;
    font-size:20px;
    line-height:2vh;
}
 header span:nth-of-type(1){
    font-size:18px;
    position:absolute;
    top:4vh;
    left:5%;
 }
 header div{
    width: 15%;
    height: 4vh;
    font-size: 16px;
    color: #848080;
    position: absolute;
    top: 3vh;
    left: 80%;
    border: 1px #e5e5e5 solid;
    border-radius: 4px;
    text-align:center;
    line-height: 4vh;
    background-color:#fefefe;
 }
 main textarea{
    width:100%;
    height:50vh;
    padding-top:2vh;
    padding-left:5%;
    border:0;
 }
 main .input .export{
    border-top:1px #e5e5e5 solid;
    height:7vh;
    background-color:#f9f9f9;
    position:relative;
 }
 #submit{
     position:absolute;
     right:5%;
     top:1vh;
 }
</style>


