<template>
  <div class="comment-container">
    <div class="comment-list" v-for="(item,index) in commentList" :key="index">
      <div class="comment-list-image">
        <img src="../../../static/img/touxiang1.jpg" alt="">
      </div>
      <div class="comment-list-body">
        <h3>{{item.commentnick_name}}</h3>
        <p>{{item.comment_content}}</p>
        <router-link :to="'/home/content/commentinfo/'+item.comment_id" class="comment-list-body-content" tag="div" :commentlistall="commentList">
          <div v-for="(item,index) in commentSubList" :key="index">
            <p><span>{{item.commentsnick_name}}:</span>{{item.comments_content}}</p>
          </div>
          <p><span>&nbsp;></span></p>
        </router-link>
        <div class="comment-list-time">
          <span>{{item.comment_date | dataFormat}}</span>
          <div class="comment-list-icon">
            <span class="mui-icon-mr mui-icon-redo"></span>
            <span class="mui-icon-extra-mr mui-icon-extra-comment"></span>
            <span class="mui-icon-extra-mr mui-icon-extra-like" @click="postcommentlike(item.user_id,item.comment_id),select(item)" :class="{'actived' : item.selected}">{{item.likecount}}</span>
          </div>
        </div>
      </div>
    </div>
    <!-- tabbar -->
    <div class="mui-card-footer">
      <input type="text" v-model="postcommentdata" placeholder="">
      <input type="button" value="" @click="postcommentMethod">
    </div>
    <!-- tabbar -->
  </div>
</template>
<script>
// import '../../../static/mui/css/mui.css'
// import '../../../static/mui/css/icons-extra.css'
import { Toast } from 'mint-ui'
export default {
  data () {
    return {
        //
        likeStatus : false,
        //id
        // dataId : this.$route.params.id,
        //
        weibocontent : {},
        //
        commentList : [],
        //
        commentSubList :[],
        //
        likeStatus : false,
        //
        postcommentdata : ''
    }
  },
  props : ["id"],
  methods : {
    showComment() {
      this.$http.get('comment/show?weiboid='+this.id).then(result => {
        if(result.body.status==1) {
          //
          this.commentList=result.body.object;
          // console.log(this.commentList)
          //list1,list1
          this.commentList.forEach(element => {
            this.commentSubList=element.list1
          });
        }
      })
    },
      //
      toggleClass() {
      this.likeStatus=!this.likeStatus
    },
    //
    postcommentMethod () {
      if(this.postcommentdata.trim().length==0) {
        return Toast('!')
      }
      this.$http.post('user/send/comment?weiboid='+this.id,{
        comment_content : this.postcommentdata.trim()
      })
      .then(result => {
        if(result.body.status==1) {
          //
           var cmt={
            head_image : '',
            commentnick_name : "",
             comment_content : this.postcommentdata.trim(),
             comment_date : Date().now,
             likecount : 0
           }
          //
          var cmts=[]
          this.commentList.unshift(cmt)
          this.commentSubList=cmts
          this.postcommentdata=''
        }
        console.log(result.body)
      })
    },
    //
    postcommentlike(userid,commentid) {
      console.log(userid+"yonghu")
      console.log(commentid+"pinglun")
      this.$http.post('user/praise/comment',{
        userid : userid,
        commentid : commentid
      }).then(result => {
        if(result.body.status==1) {
          console.log("++")
        }
      })
    },
    select (item) {
      console.log(item)
      if(!item.selected) {
         item.selected=!item.selected
         item.likecount++
      }
      else {
        item.selected=!item.selected
        item.likecount--
      }
    }
  },
  created () {
    this.showComment();
  }
}
</script>
<style lang="scss" scoped>
.comment-list {
  padding-top: 10px;
  overflow: hidden;
  .comment-list-image {
      border-radius: 50%;
      float: left;
      width: 55px;
      height: 55px;
    img {
      font-size: 0;
      line-height: 0;
      width: 100%;
      height: 100%;
    }
  }
  .comment-list-body {
    float:right;
    width: 85%;
      h3 {
        font-size: 13px;
        font-weight: bold;
        color:#858585;
        margin-bottom: 10px;
      }
      p {
        font-size: 15px;
        color: #000;
        margin-bottom: 10px;
        line-height: 22px;
      }
      .comment-list-body-content {
        background-color: #efefef;
        margin-right: 10px;
        padding: 5px;
        p {
          font-size: 13px;
          color: #000;
          span {
            color: #5f8fb7;
          }
        }
      }
  }
}
// 
  .comment-list-time {
    display:flex;
    justify-content: space-between;
    border-bottom: 1px solid #949494;
    padding: 5px 0px;
    span {
      font-size: 13px;
      color: #949494;
    }
    .comment-list-icon {
      width: 32%;
      margin-right: 10px;
      color:#636363;
      display:flex;
      justify-content:space-between;
      align-items: center;
      .noactived {
        color: #636363;
      }
      .actived {
      color:#ff8200;
    }
      .mui-icon-mr {
        font-family: Muiicons;
        font-size: 13px;
        font-weight: normal;
        font-style: normal;
        line-height: 1;
        display: inline-block;
        text-decoration: none;
        -webkit-font-smoothing: antialiased;
    }
    .mui-icon-extra-mr {
        font-family: MuiiconSpread;
        font-size: 13px;
        font-weight: normal;
        font-style: normal;
        line-height: 1;
        display: inline-block;
        text-decoration: none;
        -webkit-font-smoothing: antialiased;
  }
  }
}
// 
  .mui-card-footer {
      width:100%;
      background: #efefef;
      position:fixed;
      bottom:-1px;
      justify-content: space-around;
      z-index: 99;
      input {
        border-radius: 10px;
        margin-bottom: 0;
        margin-left: 10px;
      }
}
</style>





